figure {
	margin: 0;
}

.chat_main {
	position: fixed;
	height: 100vh;
	z-index: 99;
	left: -100%;
	top: 0;
	width: 520px;
	box-shadow: 0 0 30px 0 rgba(0,0,0,0.3);
	background-color: #fff;
	transition: 0.4s ease;
}

.chat_main.active {
	left: 0;
}

.chat_main .count {
	width: 18px;
	height: 18px;
	font-size: 10px;
	background-color: #de4682;
}

.chat_main .chat-header {
	background-color: #e5f6ff;
	padding: 30px 20px 10px;
}

.chat_main .chat-header .statustext {
	padding: 8px 20px 8px 15px;
	border-radius: 14px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	border: solid 1px #e8eef9;
	display: inline-block;
	min-width: 210px;
	font-size: 16px;
	color: #0af;
	font-weight: bold;
	background-color: #fff;
}

.chat_main .chat-header .statustext img {
	float: left;
	margin-top: 3px;
}

.chat_main .chat-header .statustext b {
	margin-right: 8px;
	color: #0af;
	font-style: normal;
}

.chat_main .chat-header .chat-close {
	padding: 8.5px 9px;
	border-radius: 14px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	background-color: #fff;
	cursor: pointer;
}

.chat_main .chat-header .chat-close:hover {
	opacity: 0.7;
}

.chat_main .chat-header .chat-back {
	display: flex;
	align-items: center;
	display: none;
}

.chat_main .chat-header .chat-back span {
	padding: 6.5px 7px;
	border-radius: 14px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	background-color: #fff;
	cursor: pointer;
	margin-left: 10px;
	display: inline-block;
}

.chat_main .chat-header .status_list {
	width: 210px;
	top: 47px;
	padding: 11px 21px 16px 53px;
}

.chat_main .chat-header .status_list li {
	display: block;
	margin: 4px 0;
	font-size: 16px;
}

.chat_main .chat-header .bottom-bar {
	margin-top: 20px;
}

.chat_main .chat-header .bottom-bar ul {
	display: flex;
}

.chat_main .chat-header .bottom-bar .search {
	width: 340px;
	height: 38px;
	border-radius: 30px;
	background-color: #fff;
	position: relative;
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
}

.chat_main .chat-header .bottom-bar .search input {
	height: 38px;
	border: none;
	border-radius: 0;
	outline: none;
	width: 89%;
	background: rgba(0,0,0,0);
	font-size: 14px;
	color: #3a4a66;
	margin: 0;
	padding: 10px 21px 10px 13px;
}

.chat_main .chat-header .bottom-bar .search input::placeholder {
	color: rgba(58,74,102,0.5);
}

.chat_main .chat-header .bottom-bar .search .closetext {
	cursor: pointer;
}

.chat_main .chat-header .bottom-bar .blue_btn {
	width: 120px;
	height: 38px;
	margin: 0px 20px 0 0;
	padding: 8px;
	border-radius: 19px;
	background: #cdedff;
	font-weight: bold;
	font-size: 14px;
	color: #0af;
	display: inline-flex;
}

.chat_main .chat-header .bottom-bar .blue_btn img {
	margin-left: 12px;
}

.chat_main .chat-accordion {
	padding: 20px;
	height: calc(100vh - 140px);
	overflow: auto;
}

.chat_main .chat-accordion::-webkit-scrollbar {
	display: none;
}

.chat_main .chat-accordion .chat_accord {
	margin-bottom: 10px;
	border: 0;
	background-color: #f4f6fa;
	border-radius: 14px;
	box-shadow: none;
	margin-top: 0px !important;
}

.chat_main .chat-accordion .chat_accord .chat_accord_head {
	cursor: pointer;
	padding: 9px 20px 9px 17px;
}

.chat_main .chat-accordion .chat_accord .chat_accord_head h2 {
	font-size: 14px;
	display: flex;
}

.chat_main .chat-accordion .chat_accord .chat_accord_head h2 i {
	position: static;
	margin-right: 8px;
}

.chat_main .chat-accordion .chat_accord .card-header {
	border: 0;
	background-color: transparent;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body {
	padding: 0px 10px 16px 10px;
	display: none;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body.body_open {
	display: block;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li {
	padding: 13px 10px 10px;
	border-radius: 14px;
	background-color: #fff;
	margin-bottom: 10px;
	width: 100%;
	position: relative;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li:last-child {
	margin-bottom: 0;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li .chat-time {
	font-size: 12px;
	opacity: 0.8;
	position: absolute;
	left: 34px;
	top: 3px;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li .chat-profile {
	width: calc(100% - 50px);
	cursor: pointer;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li .chat-profile figure {
	margin-left: 10px;
	min-width: 24px;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li .chat-profile figure img {
	max-width: 24px;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li .chat-profile figure.two-icon {
	position: relative;
	text-align: left;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li .chat-profile figure.two-icon img {
	max-width: 20px;
	position: relative;
	z-index: 2;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li .chat-profile figure.two-icon img:nth-child(2) {
	position: absolute;
	right: 0;
	top: 0;
	max-width: 14px;
	z-index: 1;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li .chat-profile h3 {
	font-size: 14px;
	font-weight: bold;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li .chat-profile p {
	font-size: 14px;
	line-height: 1.4;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li a {
	cursor: inherit;
	position: relative;
}

.chat_main .chat-accordion .chat_accord .chat_accord_body ul li a i {
	top: 50%;
	transform: translateY(-50%);
	right: -20px;
}

.chat_main .chat-screen {
	display: none;
}

.chat_main .chat-screen .msger-chat {
	padding: 0px 0px 100px;
}

.chat_main .chat-screen .msger-chat .left-msg .msg-bubble .msg-text {
	background-color: #f4f6fa;
}

.chat_main .chat-screen .msger-chat .msg-bubble {
	margin: 18px 0;
}

.chat_main .chat-screen .msger-chat .msg-bubble .msg-info {
	padding: 0 10px;
}

.chat_main .chat-screen .msger-chat .msg-bubble .msg-info-name {
	font-size: 14px;
	color: #3a4a66;
}

.chat_main .chat-screen .msger-chat .msg-bubble .msg-info-name-img {
	height: 19px;
	width: 19px;
	background-size: 100%;
	margin-left: 5px;
}

.chat_main .chat-screen .msger-chat .msg-bubble .msg-info-time {
	font-size: 12px;
	color: #3a4a66;
}

.chat_main .chat-screen .msger-chat .msg-bubble .msg-text {
	background-color: #fff;
	border-radius: 14px;
	box-shadow: 0 0 10px 0 rgba(0,0,0,0.1);
	display: block;
	font-size: 14px;
	padding: 8.5px 21px;
	margin: 8px 0 12px;
	color: #3a4a66;
}

.chat_main .chat-screen .msger-inputarea {
	padding: 22px 20px 18px;
	border-radius: 0px;
	background-color: #f4f6fa;
	position: fixed;
	width: 100%;
	bottom: 0;
	transform: translate(20px, 0px);
}

.chat_main .chat-screen .msger-inputarea .msger-input {
	width: 480px;
	height: 40px;
	padding: 8px 20px 8px 50px;
	border-radius: 20px;
	border: solid 1px #eaebf7;
	background-color: #fff;
	outline: none;
	transition: 0.5s ease all;
	border: 1.5px solid transparent;
}

.chat_main .chat-screen .msger-inputarea .msger-input:focus {
	border: 1.5px solid #e5f6ff;
}

.chat_main .chat-screen .msger-inputarea .msger-send-btn {
	transform: translate(50px, 0px);
	background: none;
	width: 35px;
	height: auto;
}

.techer-drawer {
	position: fixed;
	height: 100vh;
	z-index: 99;
	right: -100%;
	top: 0;
	width: 400px;
	box-shadow: 0 0 30px 0 rgba(0,0,0,0.3);
	background-color: #fff;
	transition: 0.4s ease;
}

.techer-drawer.active {
	right: 0;
}

.techer-drawer-close {
	padding: 8.5px 9px;
	border-radius: 14px;
	box-shadow: 0 10px 20px 0 rgba(0,0,0,0.1);
	background-color: #fff;
	cursor: pointer;
	display: inline-flex;
	margin: 30px 20px 0 0;
}

.techer-drawer-content {
	max-width: 75%;
	margin: 30px auto 0;
	text-align: center;
}

.techer-drawer-content-img {
	width: 62px;
	height: 62px;
	margin: 0 auto;
}

.techer-drawer-content-img img {
	width: 100%;
}

.techer-drawer-content h2 {
	font-size: 22px;
	color: #3a4a66;
	margin-top: 7px;
}

.techer-drawer-content p {
	margin-top: 22px;
	font-size: 14px;
	line-height: 1.29;
}

.techer-drawer-content .blue_btn {
	margin: 20px 0 0;
	display: inline-flex;
	color: #0af;
	font-size: 14px;
	padding: 10px 0px;
	text-align: center;
	justify-content: center;
	min-width: 120px;
}

